export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './divider.html';\n\nexport default class ExampleDivider extends Component {\n  static template = _tpl;\n}\n","sass":".demo {\n  :global {\n    .viewport {\n      width: 320px;\n      margin-right: 6px;\n      max-width: 100%;\n      display: inline-block;\n      vertical-align: top;\n      overflow: auto;\n      border: 1px solid rgb(0 0 0 / 12%);\n    }\n  }\n}\n","html":"<!-- import style from './divider.scss' -->\n<div :class=\"style.demo\">\n  <div class=\"viewport\">\n    <md-toolbar elevation=\"1\">\n      <span class=\"md-title\">Full</span>\n    </md-toolbar>\n\n    <md-list class=\"md-double-line\">\n      <md-subheader>Phone</md-subheader>\n\n      <md-list-item>\n        <md-icon-phone class=\"md-primary\"/>\n\n        <div class=\"md-list-item-text\">\n          <span>(650) 555-1234</span>\n          <span>Mobile</span>\n        </div>\n\n        <md-button class=\"md-icon-button md-list-action\">\n          <md-icon-sms/>\n        </md-button>\n      </md-list-item>\n\n      <md-list-item class=\"md-inset\">\n        <div class=\"md-list-item-text\">\n          <span>(650) 555-1234</span>\n          <span>Mobile</span>\n        </div>\n\n        <md-button class=\"md-icon-button md-list-action\">\n          <md-icon-sms/>\n        </md-button>\n      </md-list-item>\n\n      <md-divider></md-divider>\n      <md-subheader>Email</md-subheader>\n\n      <md-list-item>\n        <md-icon-email class=\"md-primary\"/>\n\n        <div class=\"md-list-item-text\">\n          <span>aliconnors@example.com</span>\n          <span>Personal</span>\n        </div>\n      </md-list-item>\n\n      <md-list-item class=\"md-inset\">\n        <div class=\"md-list-item-text\">\n          <span>ali_connors@example.com</span>\n          <span>Work</span>\n        </div>\n      </md-list-item>\n    </md-list>\n  </div>\n\n  <div class=\"viewport\">\n    <md-toolbar elevation=\"1\">\n      <span class=\"md-title\">Inset</span>\n    </md-toolbar>\n\n    <md-list>\n      <md-list-item>\n        <md-icon-move_to_inbox/>\n        <span class=\"md-list-item-text\">Inbox</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-send/>\n        <span class=\"md-list-item-text\">Sent Mail</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-delete/>\n        <span class=\"md-list-item-text\">Trash</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-error/>\n        <span class=\"md-list-item-text\">Spam</span>\n      </md-list-item>\n\n      <md-divider class=\"md-inset\"></md-divider>\n\n      <md-list-item>\n        <md-avatar>\n          <img src=\"https://placeimg.com/40/40/people/5\" alt=\"People\"/>\n        </md-avatar>\n\n        <span class=\"md-list-item-text\">Abbey Christansen</span>\n\n        <md-button class=\"md-icon-button md-list-action\">\n          <md-icon-chat_bubble class=\"md-primary\"/>\n        </md-button>\n      </md-list-item>\n\n      <md-list-item>\n        <md-avatar>\n          <img src=\"https://placeimg.com/40/40/people/1\" alt=\"People\"/>\n        </md-avatar>\n\n        <span class=\"md-list-item-text\">Alex Nelson</span>\n\n        <md-button class=\"md-icon-button md-list-action\">\n          <md-icon-chat_bubble class=\"md-primary\"/>\n        </md-button>\n      </md-list-item>\n\n      <md-list-item>\n        <md-avatar>\n          <img src=\"https://placeimg.com/40/40/people/6\" alt=\"People\"/>\n        </md-avatar>\n\n        <span class=\"md-list-item-text\">Mary Johnson</span>\n\n        <md-button class=\"md-icon-button md-list-action\">\n          <md-icon-chat_bubble/>\n        </md-button>\n      </md-list-item>\n    </md-list>\n  </div>\n</div>"}